import React, { Component } from 'react';
import { MDBDataTable, MDBBtn } from "mdbreact";
import './index.css';

class ButtonDropDowns extends Component {
  constructor(props) {
          super(props);
}
  state = {
    columns: [
      {
        field: 'name',
        label: 'Course Name',
        sort:'asc'
      },
      {
        field: 'fees',
        label: 'Fees',
        sort:'asc'
      },
      {
        field: 'duration',
        label: 'Duration',
        sort:'asc'
      },
      {
        field: 'availableSeat',
        label: 'Available Seat',
        sort:'asc'
      }
    ],
    rows: []
  }

  componentDidMount() {
    this.getMoney();
  }

  getMoney = () => {
    fetch("https://api.cedu.in/V2/api/getCourse", {
     method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
    })
      .then(res => res.json())
      .then(json => {
        let rows = [];
        console.log(json);
        json.response.forEach(item => rows.push({
          name: item.name,
          fees: item.fees,
          availableSeat: item.availableSeat,
          duration:item.duration,
        }));

        this.setState({ rows });
      })
      .catch(err => console.error(err));
  }

  render() {
    return (
        <MDBDataTable
          striped
          bordered
          hover
          order = {["updateAt","asc"]}
          data={{ columns: this.state.columns, rows: this.state.rows }}
        />
    );
  }
};

export default ButtonDropDowns;